.wrapper {
  border-top: 1px solid #e3e3e4;
}

.label {
  padding: 8px;
  font-size: 1.6rem;
  font-weight: 600;
}
.account-item {
  display: flex;
  padding: 8px;
  cursor: pointer;
  transition: background-color ease-in-out 200ms;

  &:hover {
    background-color: rgba(22, 24, 35, 0.03);
  }
}

.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  //   cho khỏi méo ảnh
  object-fit: cover;
}

.item-info {
  // chiếm hết chiều ngang còn lại có thể chiếm
  flex: 1;
  margin-left: 12px;
}

.nickname {
  font-size: 1.6rem;
  color: #161823;
  //   1 là tỉ lệ
  line-height: 1;
}

.check {
  margin-left: 4px;
  font-size: 1.4rem;
  color: #20d5ec;
}

.name {
  font-size: 1.2rem;
  opacity: #161823bf;
}

.more-btn {
  padding: 8px;
  color: #fe2c55;
  font-size: 1.4rem;
  font-weight: 700;
  cursor: pointer;
}
