//Menu wrapper
.menu-list {
  width: 224px;

  // nó sẽ dc ưu tiên hơn khi ở bên trong cha nó
  .menu-item {
    margin-left: 0;
  }
}

.menu-popper {
  padding-bottom: 8px;
}

.menu-body {
  overflow-y: auto;
}
//Menu Items
.menu-item {
  // kế thừa thẻ cha của nó là menu-list
  width: 100%;
  justify-content: flex-start;
  border-radius: 0;
  padding: 11px 16px;
  line-height: 1.8rem;

  // cái dấu gạch ngang của menu log out
  &.separater {
    border-top: 1px solid rgba(22, 24, 35, 0.12);
  }

  &:hover {
    background-color: rgba(22, 24, 35, 0.03);
  }
}

//Header
.header {
  position: relative;
  height: 50px;
  // để cho nó mất margin bên popper đi
  margin-top: -8px;
  flex-shrink: 0;
}

.back-btn {
  width: 50px;
  // độ cao = thèn chứa nó: .header
  height: 100%;
  background-color: transparent;
  cursor: pointer;
}

.header-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
